.App {
    background: var(--bg-color-primary);
}

/* Give all h1 a solar gradient background */

h1 {
    width: fit-content;
    background: -webkit-linear-gradient(0deg, white, var(--solar-yellow-color));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.NavbarFiller {
    width: 100vw;
    height: 10vh;
    background: var(--bg-color-primary);
}